/* add css module styles here (optional) */
@import url('https://fonts.googleapis.com/css2?family=DM+Mono:wght@300;400;500&display=swap');

._1X_hB {
  display: flex;
  padding: 3rem;
  overflow-x: scroll;
}

._1X_hB::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
._1X_hB::-webkit-scrollbar-thumb {
  background: #201c29;
  border-radius: 10px;
  box-shadow: inset 2px 2px 2px hsla(0, 0%, 100%, 0.25),
    inset -2px -2px 2px rgba(0, 0, 0, 0.25);
}

._1X_hB::-webkit-scrollbar-track {
  background: linear-gradient(90deg, #201c29, #201c29 1px, #17141d 0, #17141d);
}

._xsi02 {
  height: 90%;
  min-width: 250px;
  padding: 1.5rem;
  border-radius: 16px;
  background: #17141d;
  box-shadow: -1rem 0 3rem #000;
  display: flex;
  flex-direction: column;
  transition: 0.2s;
  margin: 0;
  scroll-snap-align: start;
  clear: both;
  position: relative;
}

._xsi02:focus-within ~ ._xsi02,
._xsi02:hover ~ ._xsi02 {
  transform: translateX(130px);
}

._xsi02:hover {
  transform: translateY(-1rem);
}

._xsi02:not(:first-child) {
  margin-left: -130px;
}

._2cQtw {
  margin-bottom: auto;
}

._2cQtw p {
  font-size: 14px;
  margin: 0 0 1rem;
  color: #7a7a8c;
}

._2cQtw h2 {
  font-size: 1.1rem;
  margin: 0.25rem 0 auto;
  text-decoration: none;
  color: inherit;
  border: 0;
  display: inline-block;
}

._2cQtw h2:hover {
  background: linear-gradient(90deg, #ff8a00, #e52e71);
  text-shadow: none;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

._3yayk {
  margin: 3rem 0 0;
  display: grid;
  grid-template-columns: 75px 1fr;
  align-items: center;
  position: relative;
}

._1Eq0N {
  grid-area: auto;
  align-self: start;
  position: relative;
  box-sizing: border-box;
}

._1Eq0N img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  filter: grayscale(100%);
  display: block;
  overflow: hidden;
  margin: 16px 10px;
}

._1W21B {
  grid-area: auto;
  box-sizing: border-box;
}

._Yddbn {
  font-style: normal;
  font-weight: 700;
  color: #7a7a8c;
}

._3T7vd {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 48px;
  fill: none;
  stroke: #ff8a00;
  stroke-width: 8;
  stroke-linecap: round;
  pointer-events: none;
}

._33Uz_ {
  margin: 1rem 0 2rem;
  padding: 0.5rem 0 1rem;
  line-height: 2;
  margin-bottom: 0;
}

._33Uz_ a {
  font-style: normal;
  font-weight: 700;
  font-size: 0.5rem;
  color: #7a7a8c;
  text-transform: uppercase;
  font-size: 0.66rem;
  border: 3px solid #28242f;
  border-radius: 2rem;
  padding: 0.2rem 0.85rem 0.25rem;
  position: relative;
}

._33Uz_ a:hover {
  background: linear-gradient(90deg, #ff8a00, #e52e71);
  text-shadow: none;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  /*-webkit-box-decoration-break: clone;*/
  background-clip: text;
  border-color: white;
}
